* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.cards {
  width: 100%;
  height: auto;
}
.card {
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_img {
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info {
  background-color: #fff;
  border-bottom-left-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}

.card_category {
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}
.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

.button {
  background-color: grey;
  border-radius: 5px;
  border: 1px solid white;
  display: flex;
  position: relative;
  justify-content: space-evenly;
  align-items: center;

  width: 100%;
  padding: 10px;
  margin-top: 20px;
  font-weight: bold;
  font-size: 14px;
}

.f {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 10px;
  border: 1px solid black;
}

.main_div {
  width: 30%;
  height: 50vh;
  background: #6983aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div {
  width: 35%;
  height: 25vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
}

.like {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 10px;
  border: 1px solid black;
}

.lodingDiv {
  height: 100vh;
  display: grid;
  place-items: center;
}

@media (max-width: 768px) {
  .card {
    float: none;
    margin: 5%;
    width: 90%;
  }
}
